import React from "react";

import "./styles.css";

function Table({ columns, records }) {
  return (
    <table className="Table">
      <thead>
        <tr>
          {columns.map(col => (col.header ? <th>{col.header}</th> : null))}
        </tr>
      </thead>
      <tbody>
        {records.map(row => [
          <tr>
            {columns.map(
              col => (col.header ? <td>{col.render(row)}</td> : null)
            )}
          </tr>,
          !columns[columns.length - 1].header ? (
            <tr className={columns[columns.length - 1].className(row)}>
              <td colspan={columns.length - 1}>
                {columns[columns.length - 1].render(row)}
              </td>
            </tr>
          ) : null
        ])}
      </tbody>
    </table>
  );
}

export default Table;



