import classnames from "classnames";
import React, { Component } from "react";
import logo from "./logo.svg";
import "./App.css";

import Card from "./components/Card";
import Code from "./components/Code";
import { Col, Row } from "./components/Grid";
import H1 from "./components/H1";
import Link from "./components/Link";
import Tag from "./components/Tag";
import Table from "./components/Table";

const code070 = `/**
 * @param {number} n
 * @return {number} n
 */

expect(
  climbStairs(0)
).toBe(1);

expect(
  climbStairs(1)
).toBe(1);

expect(
  climbStairs(2)
).toBe(2);

var climbStairs = function (n) {
  if (n <= 0) {
    return 1;
  }

  if (n < 3) {
    return n;
  }

  var waysdp = Array(n + 1);
  var i = 0;

  waysdp[0] = 1;
  waysdp[1] = 1;

  for (i = 2; i < n + 1; i++) {
    waysdp[i] = waysdp[i - 2] + waysdp[i - 1];
  }

  return waysdp[n];
}
`;

const code121 = `/**
 * @param {number[]} prices
 * @return {number}
 */

expect(
  maxProfit([])
).toBe(0);

expect(
  maxProfit([100])
).toBe(0);

expect(
  maxProfit([100, 1])
).toBe(0);

var maxProfit = function(prices) {
  var len = prices.length;

  if (len < 2) {
    return 0;
  }

  var profitdp = Array(len);
  var i = 0;
  var min = prices[0];

  profitdp[0] = 0;

  for (i = 1; i < len; i++) {
    if (prices[i] < min) {
      min = prices[i];
    }

    profitdp[i] = Math.max(profitdp[i - 1], prices[i] - min);
  }

  return profitdp[len - 1];
}
`;

const code198 = `/**
 * @param {number[]} nums
 * @return {number}
 */

expect(
  []
).toBe(0);

expect(
  [99]
).toBe(99);

expect(
  [1, 99]
).toBe(99);

var rob = function(nums) {
  var len = nums.length;

  if (len === 0) {
    return 0;
  } else if (len === 1) {
    return nums[0];
  } else if (len === 2) {
    return Math.max(nums[0], nums[1]);
  }

  var amountdp = Array(len);
  var i = 0;
  var max = 0;

  amountdp[0] = nums[0];
  amountdp[1] = nums[1];
  max = nums[0];

  for (i = 2; i < len; i++) {
    amountdp[i] = max + nums[i];

    max = Math.max(max, amountdp[i - 1]);
  }

  return Math.max(...amountdp);
};
`;

const code213 = `/**
 * @param {number[]} nums
 * @return {number}
 */

expect(
  rob([])
).toBe(0);

expect(
  rob([99])
).toBe(99);

export(
  rob([1, 99])
).toBe(99);

export(
  rob([1, 2, 99])
).toBe(99);

var rob = function(nums) {
  var len = nums.length;

  if (len === 0) {
    return 0;
  } else if (len === 1) {
    return nums[0];
  } else if (len === 2) {
    return Math.max(nums[0], nums[1]);
  }

  var dp1 = Array(len);
  var dp2 = Array(len);

  var i = 0;
  var max1 = nums[0];
  var max2 = 0;

  dp1[0] = nums[0];
  dp1[1] = nums[1];

  dp2[0] = 0;
  dp2[1] = nums[1];

  for (i = 2; i < len; i++) {
    if (i === len - 1) {
      dp1[i] = max2 + nums[i];
    } else {
      dp1[i] = max1 + nums[i];
      dp2[i] = max2 + nums[i];
    }

    max1 = Math.max(max1, dp1[i - 1]);
    max2 = Math.max(max2, dp2[i - 1]);
  }

  return Math.max(...dp1);
}
`;

const code392 = `/**
 * @param {string} s
 * @param {string} t
 * @return {boolean}
 */

expect(
  isSubsequence('', '')
).toBe(true);

expect(
  isSubsequence('', 's')
).toBe(true);

expect(
  isSubsequence('s', '')
).toBe(false);

var isSubsequence = function(s, t) {
  var lenS = s.length;
  var lenT = t.length;
  var i = 0;
  var j = 0;

  if (lenS > lenT) {
    return false;
  } else if (lenS === lenT) {
    for (i = 0; i < lenS; i++) {
      if (s.charAt(i) !== t.charAt(i)) {
        return false;
      }
    }

    return true;
  }

  var dp = Array(lenS).fill(null);

  for (i = 0; i < lenS; i++) {
    j = i === 0 ? 0 : dp[i - 1] + 1;

    for (; j < lenT; j++) {
      if (s.charAt(i) === t.charAt(j)) {
        dp[i] = j;
        break;
      }
    }
    if (dp[i] === null) {
      return false;
    }
  }

  return true;
};
`;

const code494 = `/**
 * @param {number[]} nums
 * @param {number} S
 * @return {number}
 */

expect(
  findTargetSumWays([], 99)
).toBe(0);

expect(
  findTargetSumWays([1], 99)
).toBe(0);

var findTargetSumWays = function(nums, S) {
  var len = nums.length;

  if (len === 0) {
    return 0;
  } else if (len === 1) {
    return nums[0] === S ? 1 : 0;
  } else if (len === 2) {
    if (nums[0] + nums[1] === S && nums[0] - nums[1] === S) {
      return 2;
    } else if (nums[0] + nums[1] === S || nums[0] - nums[1] === S) {
      return 1;
    }
    return 0;
  }

  var i = 0;
  var max = 0;
  var min = 0;

  for (i = 0; i < len; i++) {
    max = max + nums[i];
    min = min - nums[i];
  }



}
`;

const code740 = `/**
 * @param {number[]} nums
 * @return {number}
 */

expect(
  []
).toBe(0);

expect(
  [1]
).toBe(1);

expect(
  [1, 1, 1, 1]
).toBe(4);

var deleteAndEarn = function(nums) {
  if (nums.length === 0) {
    return 0;
  } else if (nums.length === 1) {
    return nums[0];
  }

  var len = Math.max(...nums);
  var arr = Array(len + 1).fill(0);
  var dp = Array(len + 1);
  var i = 0;
  var max = 0;

  for (i = 0; i < nums.length; i++) {
    arr[nums[i]] = arr[nums[i]] + nums[i];
  }

  max = arr[0];
  dp[0] = arr[0];
  dp[1] = arr[1];

  for (i = 2; i <= len; i++) {
    dp[i] = max + arr[i];

    max = Math.max(max, dp[i - 1]);
  }

  return Math.max(...dp);
};
`;

const code746 = `/**
 * @param {number[]} cost
 * @return {number}
 */

expect(
  minCost([])
).toBe('invalid input');

expect(
  minCost([999])
).toBe('invalid input');

var minCost = function(cost) {
  var len = cost.length;
  var top = len + 1;
  var costdp = Array(top).fill(0);
  var i = 0;

  costdp[0] = 0;
  costdp[1] = 0;

  for (i = 2; i < top; i++) {
    costdp[i] = Math.min(costdp[i - 2] + cost[i - 2],
                          costdp[i - 1] + cost[i - 1]);
  }

  return costdp[len];
}
`;

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      key: -1
    };

    this.onQuestionClick = this.onQuestionClick.bind(this);
  }

  onQuestionClick(nKey) {
    const { key } = this.state;

    this.setState({
      key: nKey === key ? -1 : nKey
    });
  }

  render() {
    const { key } = this.state;

    return (
      <div className="App">
        <Row>
          <Col>
            <H1>leetcode + js</H1>
            <Table
              columns={[
                {
                  header: "#",
                  render: ({ difficulty, key }) => (
                    <Tag
                      className={classnames({
                        danger: difficulty === "hard",
                        success: difficulty === "easy",
                        warning: difficulty === "medium"
                      })}
                    >
                      {key}
                    </Tag>
                  )
                },
                {
                  header: "Title",
                  render: ({ key, title }) => (
                    <Link onClick={() => this.onQuestionClick(key)}>
                      {title}
                    </Link>
                  )
                },
                {
                  header: "Topics",
                  render: ({ tag }) => <Tag className="outline">{tag}</Tag>
                },
                {
                  className: record =>
                    classnames({ collapsed: record.key !== key }),
                  header: null,
                  render: ({ code, description, key, title }) => (
                    <Card
                      before={key}
                      description={description}
                      onClick={() => this.onQuestionClick(-1)}
                      title={title}
                    >
                      <Code>{code}</Code>
                    </Card>
                  )
                }
              ]}
              records={[
                {
                  code: code070,
                  description: `You are climbing a stair case. It takes n steps to reach to the top.

                  Each time you can either climb 1 or 2 steps. In how many distinct ways can you climb to the top?

                  Note: Given n will be a positive integer.

                  Example 1:

                  Input: 2
                  Output: 2
                  Explanation: There are two ways to climb to the top.
                  1. 1 step + 1 step
                  2. 2 steps
                  Example 2:

                  Input: 3
                  Output: 3
                  Explanation: There are three ways to climb to the top.
                  1. 1 step + 1 step + 1 step
                  2. 1 step + 2 steps
                  3. 2 steps + 1 step
                `,
                  difficulty: "easy",
                  key: "070",
                  tag: "Dynamic Programming",
                  title: "Climbing Stairs"
                },
                {
                  code: code121,
                  description: `Say you have an array for which the ith element is the price of a given stock on day i.

                  If you were only permitted to complete at most one transaction (i.e., buy one and sell one share of the stock), design an algorithm to find the maximum profit.

                  Note that you cannot sell a stock before you buy one.

                  Example 1:

                  Input: [7,1,5,3,6,4]
                  Output: 5
                  Explanation: Buy on day 2 (price = 1) and sell on day 5 (price = 6), profit = 6-1 = 5.
                               Not 7-1 = 6, as selling price needs to be larger than buying price.
                  Example 2:

                  Input: [7,6,4,3,1]
                  Output: 0
                  Explanation: In this case, no transaction is done, i.e. max profit = 0.
                `,
                  difficulty: "easy",
                  key: "121",
                  tag: "Dynamic Programming",
                  title: "Best Time to Buy and Sell Stock"
                },
                {
                  code: code198,
                  description: `You are a professional robber planning to rob houses along a street. Each house has a certain amount of money stashed, the only constraint stopping you from robbing each of them is that adjacent houses have security system connected and it will automatically contact the police if two adjacent houses were broken into on the same night.

                Given a list of non-negative integers representing the amount of money of each house, determine the maximum amount of money you can rob tonight without alerting the police.`,
                  difficulty: "easy",
                  key: "198",
                  tag: "Dynamic Programming",
                  title: "House Robber"
                },
                {
                  code: code213,
                  description: `After robbing those houses on that street, the thief has found himself a new place for his thievery so that he will not get too much attention. This time, all houses at this place are arranged in a circle. That means the first house is the neighbor of the last one. Meanwhile, the security system for these houses remain the same as for those in the previous street.

                  Given a list of non-negative integers representing the amount of money of each house, determine the maximum amount of money you can rob tonight without alerting the police.
                  `,
                  difficulty: "medium",
                  key: "213",
                  tag: "Dynamic Programming",
                  title: "House Robber II"
                },
                {
                  code: code392,
                  description: `
                    Given a string s and a string t, check if s is subsequence of t.

                    You may assume that there is only lower case English letters in both s and t. t is potentially a very long (length ~= 500,000) string, and s is a short string (<=100).

                    A subsequence of a string is a new string which is formed from the original string by deleting some (can be none) of the characters without disturbing the relative positions of the remaining characters. (ie, "ace" is a subsequence of "abcde" while "aec" is not).

                    Example 1:
                    s = "abc", t = "ahbgdc"

                    Return true.

                    Example 2:
                    s = "axc", t = "ahbgdc"

                    Return false.

                    Follow up:
                    If there are lots of incoming S, say S1, S2, ... , Sk where k >= 1B, and you want to check one by one to see if T has its subsequence. In this scenario, how would you change your code?
                  `,
                  difficulty: "medium",
                  key: "392",
                  tag: "Dynamic Programming",
                  title: "Is Subsequence"
                },
                {
                  code: code494,
                  description: `
                    You are given a list of non-negative integers, a1, a2, ..., an, and a target, S. Now you have 2 symbols + and -. For each integer, you should choose one from + and - as its new symbol.

                    Find out how many ways to assign symbols to make sum of integers equal to target S.

                    Example 1:
                    Input: nums is [1, 1, 1, 1, 1], S is 3.
                    Output: 5
                    Explanation:
                    -1+1+1+1+1 = 3
                    +1-1+1+1+1 = 3
                    +1+1-1+1+1 = 3
                    +1+1+1-1+1 = 3
                    +1+1+1+1-1 = 3
                    There are 5 ways to assign symbols to make the sum of nums be target 3.

                    Note:
                    The length of the given array is positive and will not exceed 20.
                    The sum of elements in the given array will not exceed 1000.
                    Your output answer is guaranteed to be fitted in a 32-bit integer.
                  `,
                  difficulty: "medium",
                  key: "494",
                  tag: "Dynamic Programming",
                  title: "Target Sum"
                },
                {
                  code: code740,
                  description: `Given an array nums of integers, you can perform operations on the array.

                    In each operation, you pick any nums[i] and delete it to earn nums[i] points. After, you must delete every element equal to nums[i] - 1 or nums[i] + 1.

                    You start with 0 points. Return the maximum number of points you can earn by applying such operations.

                    Example 1:
                    Input: nums = [3, 4, 2]
                    Output: 6
                    Explanation:
                    Delete 4 to earn 4 points, consequently 3 is also deleted.
                    Then, delete 2 to earn 2 points. 6 total points are earned.

                    Example 2:
                    Input: nums = [2, 2, 3, 3, 3, 4]
                    Output: 9
                    Explanation:
                    Delete 3 to earn 3 points, deleting both 2's and the 4.
                    Then, delete 3 again to earn 3 points, and 3 again to earn 3 points.
                    9 total points are earned.

                    Note:
                    The length of nums is at most 20000.
                    Each element nums[i] is an integer in the range [1, 10000].
                  `,
                  difficulty: "medium",
                  key: "740",
                  tag: "Dynamic Programming",
                  title: "Delete and Earn"
                },
                {
                  code: code746,
                  description: `On a staircase, the i-th step has some non-negative cost cost[i] assigned (0 indexed).

                  Once you pay the cost, you can either climb one or two steps. You need to find minimum cost to reach the top of the floor, and you can either start from the step with index 0, or the step with index 1.

                  Example 1:
                  Input: cost = [10, 15, 20]
                  Output: 15
                  Explanation: Cheapest is start on cost[1], pay that cost and go to the top.

                  Example 2:
                  Input: cost = [1, 100, 1, 1, 1, 100, 1, 1, 100, 1]
                  Output: 6
                  Explanation: Cheapest is start on cost[0], and only step on 1s, skipping cost[3].

                  Note:
                  cost will have a length in the range [2, 1000].
                  Every cost[i] will be an integer in the range [0, 999].
                `,
                  difficulty: "easy",
                  key: "746",
                  tag: "Dynamic Programming",
                  title: "Min Cost Climbing Stairs"
                }
              ]}
            />
          </Col>
        </Row>
      </div>
    );
  }
}

export default App;



