import React from "react";

import H2 from "../H2";
import P from "../P";

import "./styles.css";

function Card({ before, children, description, onClick, title }) {
  return (
    <div className="Card" onClick={onClick}>
      {before && <span className="before">{before}</span>}
      {title && <H2>{title}</H2>}
      {description && <P>{description}</P>}
      {children}
    </div>
  );
}

export default Card;



